.root {
  width: 100%;
  padding-top: 20px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.root > * {
  margin-bottom: 30px;
}

.text {
  font-size: 24px;
  font-weight: 600;
  color: #202020;
}

.error {
  font-size: 16px;
  font-weight: 600;
  color: #ff5050;
}