.root {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.episode {
  width: 300px;
  padding: 20px 40px;
  margin-bottom: 20px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  text-align: left;
  text-decoration: none;
  display: flex;
  justify-content: space-between;
  cursor: pointer;
}

.episode:hover {
  background-color: rgba(250, 250, 250);
  margin-left: 10px;
  transition: margin-left 0.2s;
}

.episodeName {
  font-size: 16p;
  font-weight: 600;
  color: #202020;
}

.watchedAt {
  color: gray;
  font-weight: 600;
  font-size: 12px;
}