.root {
  width: 100%;
  padding-top: 20px;
  box-sizing: border-box;
  text-align: center;
}

.header {
  text-align: center;
  width: 380px;
  margin: auto;
  padding-bottom: 15px;
}

.headerText {
  font-size: 24px;
  font-weight: 600;
  color: #202020;
  padding-bottom: 10px;
}

.padding {
  margin: 15px 0 30px;
}

.feed {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.row {
  box-sizing: border-box;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  width: 80%;
  max-width: 900px;
  padding: 20px 40px;
  border-radius: 5px;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.text {
  font-size: 16px;
  font-weight: 600;
  color: #202020;
}

.text > a {
  color: #7049ff;
  text-decoration: none;
}

.date {
  color: gray;
  font-weight: 600;
  font-size: 12px;
}

.calendar {
  margin: auto;
  width: 80%;
  max-width: 900px;
  padding-bottom: 30px;
}

.calendarDot {
  border-radius: 30px;
}

text {
  font-size: 10px;
  font-weight: 500;
  transform: translate(0, -3px);
  fill: #838383;
}

.color-empty { fill: rgb(235, 237, 240); }
.color-scale-1 { fill: #d6e685; }
.color-scale-2,
.color-scale-3 { fill: #8cc665; }
.color-scale-4,
.color-scale-5,
.color-scale-6 { fill: #8cc665; }
.color-scale-dark { fill: #1e6823; }

.tooltip {
  background-color: white;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
  border-radius: 5px;
  position: absolute;
  z-index: 5;
  padding: 10px 20px;
  pointer-events: none;
}

.tooltipText {
  text-align: left;
  padding-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  color: #202020;
}

.tooltipText > span {
  color: gray;
}