.root {
  padding: 25px 60px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}

.logo {
  font-size: 20px;
  font-weight: 600;
  color: #202020;
  text-decoration: none;
}

.buttons > a,
.buttons > div {
  float: left;
  padding: 6px 20px;
  font-size: 14px;
  font-weight: 500;
  border-radius: 50px;
  text-decoration: none;
  color: #202020;
  cursor: pointer;
}

.buttons > a:hover,
.buttons > div:hover {
  background-color: #7A53F8;
  color: white;
}