.root {
  width: 100%;
  padding-top: 20px;
  box-sizing: border-box;
}

.header {
  width: 380px;
  margin: auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 30px;
}

.description {
  text-align: left;
}

.name {
  font-size: 24px;
  font-weight: 600;
  color: #202020;
}

.watched {
  color: gray;
  font-weight: 600;
  font-size: 16px;
}

.deleteColumn {
  text-align: center;
}

.deleting {
  color: rgb(255, 60, 60);
  font-weight: 600;
  font-size: 12px;
}